.Info {
  display: flex;
  flex-direction: column;
  max-width: 900px;
  margin: 0 auto;
  background: linear-gradient(45deg, rgb(190, 190, 190), rgb(228, 228, 228));
  padding: 10px 30px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px gray;

  .part {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
  }
}